import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-stundenanfragen',
  templateUrl: './stundenanfragen.component.html',
  styleUrls: ['./stundenanfragen.component.css']
})
export class StundenanfragenComponent implements OnInit {
  Data:Array<object>=[];
  constructor() { }

  ngOnInit() {
    $('#glyphiconIdStun').click(function() {
      if($(this).hasClass('glyphiconActive')){
        $(this).removeClass('glyphiconActive');
      }else{
        $(this).addClass('glyphiconActive');
      }
    });
    for(let i=1;i<4;i++){
      let temp={
        'First':'FirstName'+i,
        'Second':'MiddleName'+i,
        'Third':'LastName'+i,
        'Fourth':new Date().toLocaleTimeString(),
        'Fifth':'Address'+i,
        'Sixth':'LastAddress'+i
      }
      this.Data.push(temp);
    }
  }

}
