import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { KalenderComponent } from './DashBoard/kalender/kalender.component';
import { SchuleranfragenComponent } from './DashBoard/schuleranfragen/schuleranfragen.component';
import { SchulerfortschrittComponent } from './DashBoard/schulerfortschritt/schulerfortschritt.component';
import { StundenanfragenComponent } from './DashBoard/stundenanfragen/stundenanfragen.component';
import { UbersichtDeinerFahrschulenComponent } from './DashBoard/ubersicht-deiner-fahrschulen/ubersicht-deiner-fahrschulen.component';
import { VerbessereDeinProfilComponent } from './DashBoard/verbessere-dein-profil/verbessere-dein-profil.component';


const routes: Routes = [
  {path:'',component:HomeComponent},
  {path:'kalender',component:KalenderComponent},
  {path:'schuleranfragen',component:SchuleranfragenComponent},
  {path:'schulerfortschritt',component:SchulerfortschrittComponent},
  {path:'stundenanfragen',component:StundenanfragenComponent},
  {path:'ubersichtdeinerfahrschulen',component:UbersichtDeinerFahrschulenComponent},
  {path:'verbesseredeinprofil',component:VerbessereDeinProfilComponent}
];;

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
export const routingComponents=[KalenderComponent,SchuleranfragenComponent,SchulerfortschrittComponent,StundenanfragenComponent,UbersichtDeinerFahrschulenComponent,VerbessereDeinProfilComponent,HomeComponent]
