import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-schulerfortschritt',
  templateUrl: './schulerfortschritt.component.html',
  styleUrls: ['./schulerfortschritt.component.css']
})
export class SchulerfortschrittComponent implements OnInit {

  constructor() { }
  Data:Array<object>=[];
  ngOnInit() {
    $('#glyphiconIdSchulerFor').click(function() {
      if($(this).hasClass('glyphiconActive')){
        $(this).removeClass('glyphiconActive');
      }else{
        $(this).addClass('glyphiconActive');
      }
    });
    for(let i=1;i<7;i++){
      let temp={
        'Title':'FirstValue'+i,
        'Second':'Fortschritt'+i,
        'HideValue':'Lorem Ipsum is simply dummy text '+i
      }
      this.Data.push(temp);
    }
  }

}
