import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-verbessere-dein-profil',
  templateUrl: './verbessere-dein-profil.component.html',
  styleUrls: ['./verbessere-dein-profil.component.css']
})
export class VerbessereDeinProfilComponent implements OnInit {
  Data:Array<object>=[];
  constructor() { }
  ngOnInit() {
    for(let i:number=1;i<4;i++){
      this.valueToinsert(i);
    }
    
  }
  InsertNewOne=function(){
    if(this.Data.length==0){
      this.valueToinsert(1);
    }else{
      this.valueToinsert(this.Data.length+1);
    }
    
  }
  valueToinsert=function(i) {
    let temp={
      'Title':'Some Dummy Title '+i,
      'Description':'Contrary to popular belief, Lorem Ipsum is not simply random text. It has roots in a piece of classical Latin literature from 45 BC '+i,
      'ButtonName':'Button '+i
    }
    this.Data.push(temp); 
  }

}
