import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-schuleranfragen',
  templateUrl: './schuleranfragen.component.html',
  styleUrls: ['./schuleranfragen.component.css']
})
export class SchuleranfragenComponent implements OnInit {
  Data:Array<object>=[];
  constructor() { }

  ngOnInit() {
    $('#glyphiconIdSchule').click(function() {
      if($(this).hasClass('glyphiconActive')){
        $(this).removeClass('glyphiconActive');
      }else{
        $(this).addClass('glyphiconActive');
      }
    });
    for(let i=1;i<4;i++){
      let temp={
        'First':'Student'+i,
        'Second':'StudentMiddle'+i,
        'Third':'StudentLast'+i,
        'Fourth':'Address'+i
      }
      this.Data.push(temp);
    }
  }

}
